.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
  border-radius: 5px;
  padding: 0.2rem;
  text-align: center;
}

.Button:first-of-type {
  margin-left: 0;
  padding-left: 0;
}

.Success {
  background-color: #5C9210;
  color: #fff;
}

.Danger {
  background-color: #944317;
  color: #fff;
}


